*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  .App h1{
    text-align: center;
    margin: 10px;
  }
  .template{
    width: 33.333%;
    display: inline-block;
    border: 10px solid transparent;
  }
  
  .template .image{
    height: 500px;
    width: 100%;
    border: 1px solid black;
    background-size: cover;
    background-position: center;
  }
  
  .meme{
    text-align: center;
  
  }
  .meme img{
    width: auto;
    height: 400px;
    border: 1px solid black;
  }
  
  .meme input{
    margin: 10px;
    padding: 5px;
  }
  
  .meme button{
    padding: 5px;
    margin: 10px;
  }